    const total = ref("");

    function findJson() {
      let os;
      let fullArray;
      let idArray;
      let xyArray;
      let jsArray;

      if (route != null) {
        console.log("route.query:", route.query);
        if (route.query.full != null) {
          fullArray = route.query.full.split(",");
        }

        if (route.query.id != null) {
          idArray = route.query.id.split(",");
        }

        if (route.query.js != null) {
          jsArray = route.query.js.split(",");
        }

        if (route.query.os != null) {
          os = route.query.os;
        }

        if (route.query.xy != null) {
          xyArray = route.query.xy.split(",");
        }

        console.log("os:", os);
        console.log("fullArray:", fullArray);
        console.log("idArray:", idArray);
        console.log("xyArray:", xyArray);
        console.log("jsArray:", jsArray);

        if (fullArray == null) {
          return;
        }
        let count = 0;
        for (let n = 0; n < fullArray.length; n++) {
          if (fullArray[n] != null && fullArray[n] != "") {
            count += 1;
            let fulljonstep;
            joinStepArray.forEach((x) => {
              if (x.key == `${jsArray[n]}`.substring(0, 3)) {
                fulljonstep = `${jsArray[n]}:${x.text}`;
              }
            });

            let object = {
              originalAddress: os,
              fullAddress: fullArray[n],
              addressId: idArray[n],
              xy: `${xyArray[n]}`.replace(":", ","),
              joinStep: fulljonstep,
            };

            // content.items.splice(0, content.items.length);
            content.items.push(object);
          }
        }
        total.value = `${count}`;
      }
    }

